@import "~styles/variables";

.details-body {
    height: 80vh;
    width: 100%;
    display: flex;
    flex-grow: 1;
    padding: 2vh 1vw;
    &.details-body__column {
        display: flex;
        flex-direction: column;
        width: 50%;
        padding-top: 40px; }
    .details-body__par {
        text-transform: uppercase;
        color: $almost-black;
        letter-spacing: 9px; }
    .details-body__par--grey {
        color: $cool-grey;
        margin-bottom: 20px; } }

.details-body__column--left {
    img {
        height: 100px;
        width: auto; } }

.details-body__column--right {}
